//google fonts
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');

//font-awesome
// $fa-font-path:  "../fonts";
// @import "../../../node_modules/font-awesome/scss/font-awesome";

//fonts variables
$primary-font: 'Open Sans', sans-serif;

//fonts mixins
@mixin link($px) {
  color: $white;
  font-family: $primary-font;
  font-size: rem-calc($px);
  font-weight: 400;
  line-height: 1.3; 
}

@mixin text($px, $color) {
  color: $color;
  font-family: $primary-font;
  font-size: rem-calc($px);
  font-weight: 300;
  line-height: 1.3; 
}
@mixin title($px, $color) {
  color: $color;
  font-family: $primary-font;
  font-size: rem-calc($px);
  font-weight: 400;
  line-height: 1.3; 
}





